import {applyMiddleware, combineReducers, createStore} from "redux";
import {
    AboutPageReducer, AddBasketReducer, AuthReducer, BasketFetchReducer,
    CatalogPageReducer,
    ContactsPageReducer, DeliveryPageReducer,
    DetailPageReducer, FavoriteReducer, FetchFilter,
    MainPageReducer, MessageReducer, OrderHistoryReducer, PostFilterReducer, PromotionPage,
    userReducer
} from "./reducers";
import { composeWithDevTools } from 'redux-devtools-extension';
import thunk from 'redux-thunk';
const middleware = [thunk];

const reducers = combineReducers({
    userInfo: userReducer,
    mainPage: MainPageReducer,
    catalog: CatalogPageReducer,
    detailPage: DetailPageReducer,
    aboutPage: AboutPageReducer,
    promotionPage: PromotionPage,
    fetchFilter: FetchFilter,
    postFilter: PostFilterReducer,
    fetchBasket: BasketFetchReducer,
    messageAlert: MessageReducer,
    favorite: FavoriteReducer,
    contactsPage: ContactsPageReducer,
    basketPage: AddBasketReducer,
    deliveryPage: DeliveryPageReducer,
    orderHistory: OrderHistoryReducer,
    isAuthed: AuthReducer
});

export default createStore(reducers,composeWithDevTools(applyMiddleware(...middleware)));