import { InputText } from "../../../components/styled/card"
import {DropDown, Preloader} from '../../../components/global/Components';
import * as Component from '../../../components/styled/card';
import InputMask from "react-input-mask";
import {createRef, useEffect, useRef, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {getUser, messageAction, updateUser} from "../../../store/actions";
import {userSelector} from "../../../store/selectors";
import {_storage} from "../../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){
    pageContent = {
        name:'Аты',
        phone:'Телефон',
        email:'E-mail',
        country:'Ел',
        city:'Қала',
        address:'Мекенжайы',
        approve:'Қолдану',
        almaty:'Алматы',
        kz:'Қазақстан'
    }
}
if(lang =='ru'){
    pageContent = {
        name:'Имя',
        phone:'Телефон',
        email:'E-mail',
        country:'Страна',
        city:'Город',
        address:'Адрес',
        approve:'Применить',
        almaty:'Алматы',
        kz:'Казахстан'
    }
}
if(lang =='en'){
    pageContent = {
        name:'Name',
        phone:'Phone number',
        email:'E-mail',
        country:'Country',
        city:'City',
        address:'Address',
        approve:'Approve',
        almaty:'Almaty',
        kz:'Kazakhstan'
    }
}
export const Profile = () => {
    const dispatch = useDispatch();
    const [userData,setUserData] = useState();
    const user = useSelector(userSelector);
    const [info, setInfo] = useState({});
    const nodeRef = useRef(null);
    useEffect(() => {
        dispatch(getUser()).then(res => setUserData(res));
    },[]);
    const changeValue = (e) =>{
        setUserData({
            ...userData,
            user: {
                ...userData.user,
                [e.target.name]: e.target.value
            }
        });
        setInfo({
            ...info,
            [e.target.name]: e.target.value
        })
    }
    const saveData = () => {
        const keyS = Object.keys(userData.user)
        for (let x = 0 ;x < keyS.length;x++) {
            if(keyS[x] === null) continue
            if(keyS[x].length === 0) {
                dispatch(messageAction({
                    message: "Заполните все поля",
                    status: "error"
                }))
                return;
            }
        }
        dispatch(updateUser(info)).then(res => {
            dispatch(messageAction({
                message: "Профиль успешно был изменен",
                status: "success"
            }))
        });
    }
    return(
        <div className="cabinet_profile">
            {
                userData ?
                    <>
                        <p>
                            <label htmlFor="name">{pageContent.name}</label>
                            <InputText value={userData.user.name} name="name" onChange={e => changeValue(e)} type="text" id="name"/>
                        </p>
                        <p>
                            <label htmlFor="phone">{pageContent.phone}</label>
                            <InputMask mask={'+7\\799 999 99 99'}
                                       formatChar={{
                                            '9':'[0-9]'
                                        }}
                                       nodeRef={nodeRef}
                                       value={userData.user.phone_number}
                                       onChange={e => changeValue(e)}>
                                {(inputProps) =>  <InputText {...inputProps} type="text" id="phone" name="phone_number"/>}
                            </InputMask>
                        </p>
                        <p>
                            <label htmlFor="email">{pageContent.email}</label>
                            <InputText value={userData.user.email} name="email" onChange={e => changeValue(e)} type="email" id="email"/>
                        </p>
                        <div>
                            <label htmlFor="country">{pageContent.country}</label>
                            <DropDown defaultTitle={pageContent.kz} list={
                                [
                                    `${pageContent.kz}`
                                ]
                            }/>
                        </div>
                        <div>
                            <label htmlFor="city">{pageContent.city}</label>
                            <DropDown defaultTitle={pageContent.almaty} list={
                                [
                                    `${pageContent.almaty}`
                                ]
                            }/>
                        </div>
                        <p>
                            <label htmlFor="address">{pageContent.address}</label>
                            <InputText placeholder={userData.user.address ? userData.user.address : ""} type="text" id="address" name="address" onChange={e => changeValue(e)}/>
                        </p>
                        <p className="btn" onClick={saveData}>
                            <Component.Button padding="0.5rem 3rem" margin="2rem 0">{pageContent.approve}</Component.Button>
                        </p>
                    </> : <Preloader/>
            }
        </div>
    )
}