import '../../style/components/_Basket.scoped.sass';
import * as Component from '../../components/styled/card'
import { conquerBreadcump,imgImport} from '../../helpers/helper'
import {BasketLayer, BreadCumps, Custom_Basket, Preloader} from '../../components/global/Components';
import {useDispatch, useSelector} from "react-redux";
import {basketSelector, fetchBasketSelector} from "../../store/selectors";
import {useEffect, useState} from "react";
import {fetchBasketsById, getStorageBasket, removeProductFromBasket} from "../../store/actions";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'

const Basket = () =>{
    const [baskets,setBaskets] = useState({basket:null});
    const dispatch = useDispatch();
    const fetchedBasketProducts = useSelector(fetchBasketSelector);
    let pageContent = {}
    if(lang =='kz'){
        pageContent ={
            basket: 'Себет',
            empty: 'Бос',
            product:'Өнім',
            price:'Бағасы',
            count:'Саны',
            sum:'Сомасы',
            delete:'Жою'
        }
    }
    if(lang =='ru'){
        pageContent ={
            basket: 'Корзина',
            empty: 'Пусто',
            product:'Продукт',
            price:'Цена',
            count:'Количество',
            sum:'Сумма',
            delete:'Удалить'
        }
    }
    if(lang =='en'){
        pageContent ={
                basket: 'Basket',
                empty: 'Empty',
                product:'Product',
                price:'Price',
                count:'Count',
                sum:'Summary',
                delete:'Delete'
            }
    }
    useEffect(() => {
        dispatch(getStorageBasket()).then(res => {
            if( !res) return;
            const ids = Object.keys(res);
            dispatch(fetchBasketsById(ids));
            setBaskets({ ...baskets, basket: res });
            console.log(res)
        });
    },[]);

    useEffect(() => {
        console.log(baskets)
    },[baskets])

    const removeProduct = (id) => {
        dispatch(removeProductFromBasket(id)).then(res => {
            dispatch(getStorageBasket()).then(ans => {
                if( !ans) return;
                const ids = Object.keys(ans);
                dispatch(fetchBasketsById(ids));
            });
        });
    }

    console.log("fetch", fetchedBasketProducts)
    return (
        <div className="basket">
            <div className="container">
            {/* {fetchedBasketProducts.basket.data.data.products} */}
                <BreadCumps 
                    items={conquerBreadcump([["/",'Главная'],['/contacts','Контакты']])}
                />
                <Component.Container padding="2rem 0 5rem 0">
                    <Component.Title>{pageContent.basket}</Component.Title>
                    {
                        baskets.basket ?
                                fetchedBasketProducts.basket ? <div key={1}>
                                <Custom_Basket
                                        headers={[`${pageContent.product}`,`${pageContent.product}`
                                        ,`${pageContent.count}`,`${pageContent.price}`,`${pageContent.delete}`]}
                                        items={fetchedBasketProducts.basket.data.data}
                                        cachedBasket={baskets}
                                        isBasket={true}
                                        removeProduct={removeProduct}/>
                                </div> : <Preloader/>
                            : <span>{pageContent.empty}</span>
                    }
                </Component.Container>
            </div>
        </div>
    )
}

export default Basket;
