import '../../style/components/_Detail.scoped.sass';
import * as Component from '../../components/styled/card'
import {imgImport, conquerBreadcump, image_url, innerHtml} from '../../helpers/helper'
import React, {useEffect, useState} from 'react';
import {BreadCumps, Custom_Card, Preloader, ScrollWrapper} from '../../components/global/Components';
import {useDispatch, useSelector} from "react-redux";
import {Link, useParams} from "react-router-dom";
import {addBasket, detailPageAction, getStorageBasket, messageAction} from "../../store/actions";
import {basketSelector} from "../../store/selectors";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){

    pageContent ={
        main: 'Басты бет',
        bouquettes: 'Әр түрлі гүл шоқтары',
        catalog:'Каталог',
        size:'өлшемі',
        format:'тіркеу',
        basket:'Себетке қосу',
        alreadyInBasket:'Қазірдің өзінде себетке',
        interest:'Сізді қызықтыруы мүмкін',
        all:'Бөлімнің барлық гүл шоқтары',
        empty:'бос'
    }
}
if(lang =='ru'){

    pageContent ={
        main: 'Главная',
        bouquettes: 'Букеты разных типов',
        catalog:'Каталог',
        size:'Размер',
        format:'Оформление',
        basket:'В корзину',
        alreadyInBasket:'Уже в корзине',
        interest:'Вас могут заинтересовать',
        all:'Все букеты раздела',
        empty:'Пусто'
    }
}
if(lang =='en'){
    pageContent ={
        main: 'Main page',
        bouquettes: 'Bouquets of different types',
        catalog:'Catalog',
        size:'Size',
        format:'Format',
        basket:'To basket',
        alreadyInBasket:'In basket',
        interest:'You may be interested',
        all:'All bouquettes',
        empty:'Empty'
    }
}
const Detail = (props) =>{
    const {
        id
    } = useParams()
    const dispatch = useDispatch();
    const [choosen,setChoosen] = useState({
        count: 1
    });
    const [curDetail,setDetail] = useState({});
    const baskets = useSelector(basketSelector);

    useEffect(() => {
        dispatch(getStorageBasket()).then(res => {
            if (!res) return;
            if(res[id]) setChoosen(res[id]);
        })
        dispatch(detailPageAction(id)).then(res => {
            setDetail(res);
            console.log(res)
        });
    },[]);

    const validate = () => {
        const message = {
            message: "Выберите ",
            status: "error"
        }
        if(!choosen['size'])
            dispatch(messageAction({...message,message: message.message + "размер букета"}))
        else if(!choosen['format'])
            dispatch(messageAction({...message,message: message.message + "тип букета"}))
        console.log(choosen)
        return choosen['format'] && choosen['size'];
    }

    const storeToBasket = () => {
        if(!validate()) return;
        dispatch(addBasket({[id]: choosen}));
        dispatch(getStorageBasket());
    }

    const onAddChoosen = (name,value) => {
        setChoosen({
            ...choosen,
            [name]: value
        })
    }

    return (
        <>
            <div className="detail">
                <div className="container">
                    {
                        curDetail.product ?
                        <>
                            <BreadCumps
                                items={conquerBreadcump([['/',`${pageContent.main}`],['/catalog',`${pageContent.bouquettes}`],['/berry',curDetail['product']['name']]])}
                            />
                            <Component.Container padding="2rem 0">

                                <div className="detail_content">
                                    <div className="imageIn">
                                        <img src={curDetail['product']['image'] ? image_url + curDetail['product']['image'] : imgImport('default_text', 'bucket_detail.png')} alt=""/>
                                    </div>
                                    <div>
                                        <Component.Title margin="0 0 1.6rem 0" align="left">{ curDetail['product']['name'] }</Component.Title>
                                        <p dangerouslySetInnerHTML={innerHtml(curDetail['product']['description'])}/>
                                        <div className="detail_content_size">
                                            <span className="detail_content_title">{pageContent.size}</span>
                                            <div className="sizes">
                                                {
                                                    curDetail['product']['sizes'].length === 0
                                                        ?
                                                        "Размеры не найдены" :
                                                        curDetail['product']['sizes'].map((obj,key) => {
                                                            return(
                                                                <span className={choosen['size'] === obj.slug ? 'active' : ''} key={key} onClick={() => onAddChoosen('size',obj.slug)}>{obj.name}</span>

                                                            )
                                                        })
                                                }
                                            </div>
                                        </div>
                                        <div className="detail_content_decoration">
                                            <span className="detail_content_title">{pageContent.format}</span>
                                            <div className="select">
                                                {
                                                    curDetail['product'].formats.length === 0 ? "Формат букета не найден" :
                                                        <>
                                                            {
                                                                curDetail['product'].formats.map((obj, index) => {
                                                                    return (
                                                                            <p key={index}>
                                                                                <input type="radio" name={obj.slug}
                                                                                       id={obj.slug} checked={choosen['format'] === obj.slug}
                                                                                    onChange={(e) => onAddChoosen('format',obj.slug)}/>
                                                                                <span className="radio_control"/>
                                                                                <label htmlFor={obj.slug}>{obj.name}</label>
                                                                            </p>

                                                                    )
                                                                })
                                                            }
                                                        </>
                                                }
                                            </div>
                                        </div>
                                        <div className="detail_content_prices">
                                            <span className="price">{Math.round(curDetail['product']['price'] - (curDetail['product']['price'] * curDetail['product']['discount'])/100 )} ₸</span>
                                            <span className="discount">{curDetail['product']['discount'] ? curDetail['product']['price'] : '0'} ₸</span>
                                            {
                                                baskets.basket ? (!baskets.basket[id] ?
                                                    <Component.Button padding="0.7rem 3rem" onClick={storeToBasket}>{pageContent.basket}</Component.Button> :
                                                    <Component.Button padding="0.6rem 2.6rem" background="white" border="none" color="#E9AAA6">{pageContent.alreadyInBasket}</Component.Button>): null
                                            }
                                        </div>
                                    </div>
                                </div>
                                <Component.TypeOfPay margin="3rem 0 0 0">
                                    {

                                        curDetail['benefits'].map((obj,key) => (
                                                <div className="inner" key={key}>
                                                    <img src={image_url + obj.image} alt="" />
                                                    <p>
                                                        {obj.name}
                                                    </p>
                                                </div>
                                        ))
                                    }
                                </Component.TypeOfPay>
                                <div className="detail_interest">
                                    <Component.Title margin="4rem 0 1rem 0">{pageContent.interest}</Component.Title>
                                    <Component.CardsContainer>
                                        {
                                            typeof curDetail['interesting'] === "Array" ? curDetail['interesting'].map((obj,index) => {
                                                return (
                                                    <Custom_Card params={
                                                        {
                                                            width: '32%',
                                                            image: imgImport('catalog', 'card-1.jpg'),
                                                            price: `${obj.price} ₸`,
                                                            title: obj.name,
                                                            is_popular: obj.is_popular,
                                                            discount: obj.discount
                                                        }
                                                    } index={index}/>
                                                )
                                            }) : <span style={{"margin": "0 auto"}}>{pageContent.empty}</span>
                                        }
                                    </Component.CardsContainer>
                                    <div className="item-center">
                                        <Link to="/catalog">
                                            <Component.Button background="white" shadow="none" margin="3rem 0"
                                                              border="2px solid #E9AAA6" color="#E9AAA6">{pageContent.all}</Component.Button>
                                        </Link>
                                    </div>
                                </div>
                            </Component.Container>
                        </>: <Preloader/>
                    }
                </div>
            </div>
        </>
    )
}

export default ScrollWrapper(Detail);