import styled from 'styled-components'

const brown = '#37130F';
const rose = "#E9AAA6";
const lightBrown = "#866663";
const tooLight = "#DC8E89";
const gray = "#606C66";

export const BannerCard = styled.div`
    background-image: url(${props => props.backgroundImage});
    background-size: cover;
    height: auto;
    background-position: center;
    border-radius: 22px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    @media (max-width: 767px){
        height: auto;
    }
`

export const Button = styled.button`
    border: none;
    background-color: ${props=> props.background ? props.background : "#E9AAA6"};
    border: ${props => props.border ? props.border : "none"};
    width: auto ;
    margin: ${props => props.margin ? props.margin : "0"};
    color: ${props => props.color ? props.color : "white"};
    padding: ${props => props.padding ? props.padding : "0.8rem 3rem"};
    border-radius: 100px;
    font-weight: 600;
    transition: .3s all;
    border: ${props => props.border ? props.border : "1.4px solid "+rose};
    box-shadow: ${props => props.shadow ? props.shadow : "rgba(194,130,126,0.3) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;"};
    a{
        text-decoration: none;
        color: ${props => props.color ? props.color : "white"}  
    }
`
export const TypeOfPay = styled.div`
    display: flex;
    margin-top: 3.5rem;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    .inner{
        text-align: center;
        width: ${props => props.width};
        margin: ${props => props.margin ? props.margin : ""};
        height: ${props => props.height ? props.height : "120px" };
        &>img{
            height: ${props => props.imgHeight ? props.imgHeight : "42px"};
            object-fit: contain;
        }
        &>p{
            /* text-overflow: ellipsis; */
            /* white-space: pre-wrap; */
            margin: 0 auto;
            margin-top: 20px;
            width: ${props=>props.cardWidth ? props.cardWidth : "80%"}
        }
        &_title{
            color: ${gray};
        }
        &_detail{
            p{
                margin: 0;
                font-weight: 500;
                color: ${brown}
            }
        }
    }

    @media (max-width: 480px){
       
        img{
            height: 30px !important;
        }

        p{
            /* font-size: .7rem; */
        }
        .inner{
            width: 40%;
            height: 100px;
        }
    }
    
`
export const Link = styled.span`
    display: inline-block;
    text-decoration: underline;
    color: ${brown};
    cursor: pointer;
    margin: ${props=>props.margin ? props.margin : "1rem 0 0 0"};
`
export const customCheckbox = styled.span`

    display: flex;
    align-items: center;
    position: relative;
    label{
        margin-left: 10px;
    }
    
    &>input:checked ~ span{
        &:after{
            transform: scale(1);
        }
    }
    &>span{
        border: 1px solid #D5D5D5;
        border-radius: 4px;
        width: 21px;
        height: 21px;
        position: relative;
        display: inline-block;
        &:after{
            width: 79%;
            position: absolute;
            transition: 0.3s all;
            left: 10%;
            top: 12%;
            border-radius: 4px;
            background-color: #E9AAA6;
            content: "";
            transform: scale(0);
            height: 79%;
        }
    }
        
    &>input{
        border: none;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 2;
        height: 21px;
        width: 21px;
        // width: 21px;
        // height: 21px
        cursor: pointer;
        opacity: 0;
    }

`
export const CategoryInner = styled.div`
    border-radius: 20px;
    background-size: cover;
    margin-top: 3.5rem;   
    height: 290px;
    background-image: url(${props => props.image});
    padding: 2rem 1.9rem;
    width: 32%;
    &>div{
        text-align: right;
        color: ${brown};
        font-size: 2rem;
        font-weight: 500;
        font-family: 'Cormorant';
        width: 50%;
        margin-left: auto;
    }
    @media (max-width: 480px){
        width: 100%;
        margin: 1rem 0;
    }
`
export const Tabs = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    &>div{
        margin-left: 5rem;
        font-size: 1.2rem;
        cursor: pointer;
        color: ${gray};
    }
    .active{
        color: ${rose};
        border-bottom: 1.3px solid ${rose};
    }
    @media (max-width: 480px){
        overflow: scroll;
        &>div{
            width: 25%;
            margin-left: 2rem;
            font-size: 1rem;
            padding: 10px 0
        }   
    }
`;

export const Preloader = styled.div`
    border-radius: 100%;
    width: 75px;
    height: 75px;
    margin: 0 auto;
    position: relative;
    display: ${ props => props.isStop ? 'none' : 'inline-block' };
    div{
        border-radius: 50%;
        width: 50px;
        position: absolute;
        height: 50px;
        border: 6px solid #fff;
        animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        border-color: #fff ${rose} ${rose} ${rose};
        &:nth-child(1) {
          animation-delay: -0.45s;
        }
    }
    @keyframes lds-ring {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
`;

export const Container = styled.div`
    padding: ${props => props.padding ? props.padding : ""};
`;

export const Title = styled.div`
    margin: ${props => props.margin ? props.margin : "0"};
    text-align: ${props => props.align ? props.align : "center"};
    font-size: ${props => props.font ? props.font : '3rem'};
    font-family: 'Cormorant';
    font-weight: 500;
    color: ${props => props.color ? props.color : brown};
    @media (max-width: 767px){
        margin: 1rem 0;
    }
`
export const CardsContainer = styled.div`
    /* margin-top: 2.5rem; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    width: ${props => props.width ? props.width : "auto"};
    @media (max-width: 480px){
        display: flex;
    }
`
export const Card = styled.div`
    width: 100%;
    width: ${props => props.width ? props.width : "30%"};
    /* margin: 0 10px; */
    border-radius: 20px;
    background: #FCFBFB;
    overflow: hidden;
    box-shadow: 0rem 4.8rem 7rem -8rem #333;
    margin-top: ${props => props.margin ? props.margin : "2.5rem"};
    position: relative;
    transition: 0.3s ease-out;
    @media (max-width: 480px){
        width: 100%;
        font-size: 1.1rem;
    }
    &:hover{
        transform: scale(1.033);
        box-shadow: 0rem 4.8rem 4rem -8rem #333;
        z-index: 30;
    }
    &>img{
        height: 300px;
        object-fit: cover;
        width: 100%;
    }
    .content_stock{
        .title{
            font-weight: 500;
        }
        &>div{
            text-align: justify;
            .description{
                color: ${gray};
            }
        }
    }   
    .hints{
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 25px;
        left: 20px;
        &>span{
            background-color: white;
            margin-bottom: 12px;
            border-radius: 10px;
            padding: 11px 4px;
            text-align: center;
            font-weight: 600;
            &:first-child{
                color: ${lightBrown};
            }
            &:last-child{
                color: ${tooLight};
            }
        }
    }
    .content{
        text-align: center;
        &>span{
            font-size: 1.5rem;
            margin-top: 20px;
            color: ${lightBrown};
            height: auto;
            width: 100%;
            a{
                color: ${lightBrown};
                display: inline-block;
                overflow: hidden;
                width: 90%;
                display: inline-block;
                white-space: nowrap;
                text-overflow: ellipsis;
                text-decoration: none;
            }
            display: inline-block;
        }
        .inner{
            display: flex;
            padding: 1.2343rem 1.5rem;
            justify-content: space-between;
            .price{
                font-size: 1.5rem;
                color: ${brown};

            }
            .discount{
                color: ${gray};
                display: inline-block;
                margin-left: 17px;
                text-decoration-line: line-through;
                display: inline-block;
            }
            &>img{
                object-fit: contain;
            }
        }
        button{
            margin-bottom: 1.532rem;
            /* box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px; */
        }
    }
    @media (max-width: 768px){
        &>img{
            height: 150px;
        }
    }
`
export const InputText = styled.input`
    border-radius: 100px;
    width: ${props => props.width ? props.width : "80%"};
    padding: ${props => props.padding ? props.padding : "0.5rem 2rem"} ;
    border: none;
    border: 1px solid #D5D5D5;
    outline: none;
    color: ${gray};
`
export const TextArea = styled.textarea`
    border-radius: 10px;
    width: ${props => props.width ? props.width : "80%"};
    padding: ${props => props.padding ? props.padding : "0.5rem 2rem"} ;
    border: none;
    border: 1px solid #D5D5D5;
    outline: none;
    color: ${gray};
`
export const DropDownContainer = styled.div`
    width: ${props => props.width ? props.width : '80%'};
    position: relative;
    .dropdown_current{
        border: 1px solid #D5D5D5;
        border-radius: 100px;
        padding: ${props => props.padding ? props.padding : "0.5rem 2rem"} ;
        display: flex;
        align-items: center;
        cursor: pointer;
        justify-content: space-between;
        color: ${gray};
    }
    .dropdown_list{
        position: absolute;
        top: 100%;
        z-index: 4;
        border: ${props => props.maxHeight == "0" ? "none" : "1px solid #D5D5D5"};
        border-top: none;
        overflow: hidden;
        max-height: ${props=> props.maxHeight ? props.maxHeight : "0"};
        background-color: white;
        width: 100%;
        transition: 0.2s all;
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 20px;
        &>div{
            transition: 0.3s all;
            text-align: left;
            color: ${gray};
            cursor: pointer;
            text-transform: capitalize;
            padding: 0.5rem 2rem;
            &:hover{
                background-color: #FAF6F6;
            }
        }
    }

`