import {
    aboutState,
    authState, basketState,
    catalogState,
    contactsState,
    deliveryState,
    detailState, favoritesState, fetchedBasket, fetchFilter,
    mainPageState, messageState, orderHistory, promotionState,
    userState
} from "./states";
import {actions} from "./constants";
import {_storage} from "../helpers/helper";

export const userReducer = (state = userState, {type, payload}) => {
    switch (type){
        case actions.USER_ACTION:
            return {...state, user: payload};
        case actions.REGISTER_ACTION:
            return {...state, user: payload};
        case actions.GET_USER:
            return {...state, ...payload}
        default:
            return state;
    }
}

export const CatalogPageReducer = (state=catalogState,{type,payload}) => {
    switch (type) {
        case actions.PRODUCT_FETCH:
            console.log("PAYLOAD", payload)
            const new_payload = {
                ...payload,
                products: {
                    data: [...payload]
                }
            }
            return {...state, ...new_payload};
        default:
            return {...state}
    }
}

export const BasketFetchReducer = (state=fetchedBasket,{type,payload}) => {
    switch (type) {
        case actions.BASKET_FETCH_BY_ID:
            return {...state, basket: {...payload}};
        default:
            return {...state}
    }
}

export const PostFilterReducer = (state=fetchFilter,{type,payload}) => {
    switch (type) {
        case actions.POST_FILTER:
            return {...state, ...payload};
        default:
            return {...state}
    }
}

export const MessageReducer = (state=messageState,{type,payload}) => {
    switch (type) {
        case actions.MESSAGE_ALERT:
            return {...state, ...payload};
        default:
            return {...state}
    }
}

export const FavoriteReducer = (state=favoritesState,{type,payload}) => {
    switch (type) {
        case actions.FAVORITE_ACTION:
            return {...state, products: {...payload}};
        case actions.FETCH_FAVORITE:
            return {...state, ...payload}
        default:
            return {...state}
    }
}


export const MainPageReducer = (state = mainPageState, {type, payload}) => {
    switch (type) {
        case actions.MAIN_PAGE:
            return {...state, contents: payload};
        case actions.MAIN_PAGE_TAB:
            return {...state, tab: payload};
        case actions.MAIN_PAGE_PRODUCTS:
            return {
                ...state,
                contents: {
                    ...state.contents,
                    products: {
                        data: [...payload]
                    }
                }
            };
        
        default:
            return state;
    }
}

export const DetailPageReducer = (state = detailState, {type, payload})=> {
    switch (type){
        case actions.DETAIL_PAGE:
            return {...state, content: payload}
        default:
            return {...state};
    }
}

export const AddBasketReducer = (state = basketState, {type, payload})=> {
    switch (type){
        case actions.ADD_BASKET_ACTION:
            return {...state, basket: {
                    ...state.basket,
                    payload
                }};
        case actions.FETCH_BASKET:
            return  {
                ...state,basket : {...payload}
            }
        default:
            return {...state};
    }
}

