import '../../style/components/_Catalog.scoped.sass';
import * as Component from '../../components/styled/card'
import {_url, image_url, imgImport, innerHtml} from '../../helpers/helper'
import {createRef, useEffect, useRef, useState} from 'react';
import {BreadCumps, Custom_Card, Preloader, ScrollWrapper} from '../../components/global/Components';
import Pagination from '../../components/global/Pagination'
import {useDispatch, useSelector} from "react-redux";
import {basketSelector, catalogSelector, favoritesSelector, filterSelection} from "../../store/selectors";
import {
    catalogProducts,
    filterAction,
    getFavoriteAction,
    getStorageBasket, messageAction,
    postFilterAction,
    fetchProductsBySlug
} from "../../store/actions";
import {useHistory, useParams} from "react-router-dom";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){

    pageContent ={
        main: 'Басты бет',
        bouquettes: 'Әр түрлі гүл шоқтары',
        catalog:'Каталог',
    }
}
if(lang =='ru'){

    pageContent ={
        main: 'Главная',
        bouquettes: 'Букеты разных типов',
        catalog:'Каталог',
    }
}
if(lang =='en'){
    pageContent ={
        main: 'Main page',
        bouquettes: 'Bouquets of different types',
        catalog:'Catalog',
    }
}
const Catalog = () =>{
    const [isOpen,setOpen] = useState(false);
    const history = useHistory();
    const [res,setRes] = useState();
    const openFilter = () => {
        setOpen(!isOpen);
    }
    const dispatch = useDispatch();
    const [elRefs, setElRefs] = useState([]);
    const [catalog,setCatalog] = useState([]);
    const filterSelector = useSelector(filterSelection);
    const favorites = useSelector(favoritesSelector);
    const{
        id = 1
    } = useParams();
    const [paginationDetails, setPaginationDetails] = useState(null);

    const onPaginate = ({ currentPage }) => {
        console.log(currentPage)
        if(currentPage <= 0 || !currentPage) return;
        history.push('/catalog/'+currentPage);
        !checkers['category'] ? dispatch(catalogProducts(currentPage)).then(res => {
            setCatalog(res);
            dispatch(filterAction());
            setPaginationDetails({...res.products});
            dispatch(getStorageBasket());
            dispatch(getFavoriteAction());
        }) : dispatch(postFilterAction(res,currentPage)).then(res => {
            setCatalog({
                ...catalog,
                products: {
                    ...res.meta,
                    links: res.links,
                    data: res.data
                }
            })
        })
        window.scrollTo(0,0);
    }
        console.log("PAGINATION", paginationDetails)
    const baskets = useSelector(basketSelector);
    const [checkers, setCheckers] = useState({'category': ""});
    const [priceFields] = useState([{
        name:" till15",
        min: 0,
        max: 15000,
        text: "до 15,000"
    },{
        name:" till25",
        min: 15000,
        max: 25000,
        text: "от 15,000-до 25,000"
    },{
        name: "till50",
        min: 25000,
        max: 50000,
        text: "от 25,000-до 50,000",
    },{
        name: "forMore",
        min: 50000,
        max: 10000000,
        text: "от 50,000"
    }])

    useEffect(() => {
        setElRefs(elRefs => (
            Array(16).fill().map((_, i) => elRefs[i] || createRef())
        ));
        dispatch(catalogProducts(id)).then(res => {
            const new_res = {
                ...res,
                products: {
                    data: [...res.products.data]
                }
            }
        
            setCatalog({...new_res});
            dispatch(filterAction());
            setPaginationDetails({...res.products});
            dispatch(getStorageBasket());
            dispatch(getFavoriteAction());
        });
    }, []);
    console.log("CATALOG", catalog)

    const changeFields = (target,e,index) =>{
        if(target === 'check_btn') {
            setCheckers({
                ...checkers,
                [target]: index
            })
            return;
        }
        const value = filterSelector['filter'][target][index].slug;

        e.target.checked ? setCheckers({
            ...checkers,
            [target]: checkers[target] ? [...checkers[target], value] : [value]
        }) : setCheckers({
            ...checkers,
            [target]: checkers[target].filter(x => x !== value)
        });

    }

    const changeCategory = (index) => {
        setPaginationDetails(null);

        // dispatch(postFilterAction({ category: catalog['categories'][index].slug }, 1)).then(res => {
        //     console.log("RESSSS", res)
        //     setCatalog({
        //         ...catalog,
        //         products: {
        //             total: res.meta.total,
        //             current_page: res.meta.current_page,
        //             links: res.links,
        //             data: res.data
        //         }
        //     });
        //     setPaginationDetails({...res.meta});
        // });
        // filterProducts();
        dispatch(fetchProductsBySlug(catalog['categories'][index].slug))
            .then(res => {
                console.log("Res####", res);
                setCatalog({
                ...catalog,
                products: {
                    ...res.meta,
                    links: res.links,
                    data: res.data
                }
            })
            setPaginationDetails({...res.meta});
            });

        setCheckers({
            ...checkers,
            'category': catalog['categories'][index]
        })
    }

    const filterProducts = () => {
        if(!checkers['category']) {
            dispatch(messageAction({
                message: "Выберите категорию",
                status: "error"
            }))
            return;
        }
        const new_res = {};
        new_res['category'] = checkers['category'].slug;
        new_res["price_from"] = priceFields[checkers["check_btn"]] ? priceFields[checkers["check_btn"]].min : 0;
        new_res["price_to"] = priceFields[checkers["check_btn"]] ? priceFields[checkers["check_btn"]].max : 100000;
        new_res["size"] = checkers["sizes"];
        new_res["format"] = checkers["formats"];
        new_res["type"] = checkers["types"];

        setRes({...new_res});

        setPaginationDetails(null);

        dispatch(postFilterAction(new_res,id)).then(res => {
            setCatalog({
                ...catalog,
                products: {
                    total: res.meta.total,
                    current_page: res.meta.current_page,
                    links: res.links,
                    data: res.data
                }
            });
            setPaginationDetails({...res.meta});
        });

        window.scrollTo(0,0);
    }

    const checkIsFavorite = (obj,index) => {
        if(!Array.isArray(favorites.products )) {
            dispatch(getFavoriteAction());
            return false;
        }
        const cur = favorites.products.map((obj,index) => {
            return obj.id;
        });
        return cur.includes(obj.id);
    }

    const clearFilters = () => {
        Object.keys(elRefs).forEach(res => {
            if(elRefs[res].checked){
                elRefs[res].checked = false;
            }
        });
        setCheckers({});
        window.scrollTo(0,0);
    }

    return (
        <div>
            <div className="catalog">
                <div className="container">
                    <BreadCumps 
                        items={
                            [
                                {
                                    link: "/",
                                    name: pageContent.main
                                },
                                {
                                    link: "/",
                                    name: pageContent.bouquettes
                                },
                                {
                                    link: "/",
                                    name: checkers['category']? checkers['category'].name : pageContent.catalog
                                }
                            ]
                        }
                    />
                    <Component.Title margin="2rem 0 0 0">{checkers['category'] ? checkers['category'].name : "Каталог"}</Component.Title>
                    {
                            catalog.products ?
                            <>
                                <div className="catalog_inner">
                                    <div className="catalog_inner_filterMb" onClick={openFilter}>
                                        <div>
                                            <h4>Каталог</h4>
                                            <img src={imgImport('default_text','arrow_bottom.svg')} alt="" />
                                        </div>
                                    </div>
                                    <div className={isOpen ? 'catalog_inner_filter openFilter' : 'catalog_inner_filter'}>
                                        <div className="catalog_inner_filter_catalogs">
                                            <h4>Каталог</h4>
                                            <div>
                                                {
                                                    catalog['categories'].map((obj,index) => {
                                                        return (
                                                            <span
                                                                key={index}
                                                                onClick={() => changeCategory(index)} className={checkers['category'] ? (checkers['category'].slug === obj.slug ? "active" : "") :'' }>{ obj.name }</span>
                                                        )
                                                    })
                                                }
                                            </div>
                                        </div>
                                        <div className="catalog_inner_filter_filter-price">
                                            <h4>Цена</h4>
                                            <div className="select">
                                                {
                                                    priceFields.map((obj,index) => {
                                                        return (
                                                            <p key={index}>
                                                                <input type="radio"
                                                                       name={obj.name}
                                                                       id={obj.name}
                                                                       ref={ref => elRefs["category" + index] = ref}
                                                                       checked={index === checkers['check_btn']}
                                                                       onChange={(e) => changeFields('check_btn',e,index)}/>
                                                                <span className="radio_control"/>
                                                                <label htmlFor={obj.name}>{obj.text}</label>
                                                            </p>
                                                        )
                                                    })
                                                }
                                            </div>
                                        </div>
                                        <div className="catalog_inner_filter_filter-types">
                                            <h4>Тип букета</h4>
                                            <div>
                                                {
                                                    // eslint-disable-next-line array-callback-return
                                                    filterSelector.filter ? filterSelector['filter']['types'].map((obj,index) => {
                                                            return (
                                                                <p key={index}>
                                                                    <input type="checkbox"
                                                                           ref={ref => elRefs["type" + index] = ref}
                                                                           id={obj.name}
                                                                           onChange={(e) => changeFields('types',e,index)}/>
                                                                    <span/>
                                                                    <label htmlFor={obj.name}>{obj.name}</label>
                                                                </p>
                                                            )

                                                    }) : <Preloader/>
                                                }
                                            </div>
                                        </div>
                                        <div className="catalog_inner_filter_filter-types">
                                            <h4>Форматы букета</h4>
                                            <div>
                                                {
                                                    // eslint-disable-next-line array-callback-return
                                                    filterSelector.filter ? filterSelector['filter']['formats'].map((obj,index) => {
                                                        return (
                                                            <p key={index}>
                                                                <input
                                                                    ref={ref => elRefs["format" + index] = ref}
                                                                    type="checkbox"
                                                                    id={obj.name} onChange={(e) => changeFields('formats',e,index)}/>
                                                                <span/>
                                                                <label htmlFor={obj.name}>{obj.name}</label>
                                                            </p>
                                                        )

                                                    }) : <Preloader/>
                                                }
                                            </div>
                                        </div>
                                        <div className="catalog_inner_filter_filter-types">
                                            <h4>Размеры букета</h4>
                                            <div className="sizes">
                                                {
                                                    // eslint-disable-next-line array-callback-return
                                                    filterSelector.filter ? filterSelector['filter']['sizes'].map((obj,index) => {
                                                        return (
                                                            <p key={index}>
                                                                <input type="checkbox"
                                                                       ref={ref => elRefs["size" + index] = ref}
                                                                       id={obj.name} onChange={(e) => changeFields('sizes',e,index)}/>
                                                                <span/>
                                                                <label htmlFor={obj.name}>{obj.name}</label>
                                                            </p>
                                                        )

                                                    }) : <Preloader/>
                                                }
                                            </div>
                                        </div>
                                        <div className="catalog_inner_filter_btn">
                                            <Component.Button onClick={filterProducts}>
                                                Применить
                                            </Component.Button>
                                            <Component.Link onClick={clearFilters}>
                                                Сбросить фильтры
                                            </Component.Link>
                                        </div>
                                    </div>
                                    <div class="HUINIYA">
                                       
                                        {
                                            catalog.products.data ? (catalog.products.data.length === 0 ? <span style={{margin: "2rem"}}>Продукты не найдены</span> : null) :null
                                        }
                                        <>
                                            <Component.CardsContainer >
                                            {
                                                catalog.products.data.length % 3 === 2 ? catalog.products.data.push({
                                                    "name" : "",
                                                    "price" : ""
                                                })  : null
                                            }
                                            {
                                                catalog.products.data ? catalog.products.data.map((obj,index) => {    
                                                    return (
                                                        obj.name ? <div key={index} className="w-32">
                                                                <Custom_Card params={
                                                                            {
                                                                                width: '32%',
                                                                                image: obj.image ? image_url + obj.image : imgImport('catalog','card-1.jpg'),
                                                                                price: obj['price'],
                                                                                title: obj['name'],
                                                                                is_popular : obj.is_popular,
                                                                                discount: obj.discount
                                                                            }
                                                                        }
                                                                        isFavorite={favorites.products ? checkIsFavorite(obj,index) : false}
                                                                        index={obj.id}
                                                                        isActive={baskets.basket ? (baskets.basket[obj.id] === undefined) : true}
                                                                />
                                                            </div> : <div style={{width: "32%"}}/>
                                                    )
                                                }) : null
                                            }
                                        </Component.CardsContainer>
                                            <div className="catalog_pagination">
                                                <div className="catalog_pagination_inner">
                                                 
                                                    {
                                                    
                                                    paginationDetails ?
                                                        <>
                                                            {paginationDetails.total <= 9 ? null :
                                                                <img src={imgImport('slider','left-arrow.svg')} alt="" /> }
                                                            <Pagination
                                                                totalRecords={paginationDetails.total}
                                                                pageLimit={9}
                                                                pageNeighbours={1}
                                                                onPageChanged={onPaginate}
                                                                currentPage={catalog['products']['current_page']}
                                                            />
                                                            {paginationDetails.total <= 9 ? null :
                                                                <img src={imgImport('slider','right-arrow.svg')} alt="" /> }
                                                        </>
                                                    : <Preloader/>
                                                }
                                            </div>
                                        </div>
                                        </>
                                                
                                    </div>
                                </div>
                                <div className="catalog_about">
                                    <Component.Title align="left" margin="3rem 0 1rem 0">{catalog['about']['name']}</Component.Title>
                                    <p dangerouslySetInnerHTML={innerHtml(catalog['about']['description'])}/>
                                </div>
                            </>
                            : <Preloader  />
                    }
                </div>
            </div>
        </div>
    )
}
export default ScrollWrapper(Catalog);