import '../../style/components/_Auth.scoped.sass';
import * as Component from '../../components/styled/card'
import {AuthLayer} from '../../components/global/Components';
import React  from 'react';
import { Link } from 'react-router-dom';
import {registerAction} from "../../store/actions";
import InputMask from "react-input-mask";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){

    pageContent ={
        name: 'Сіздің атыңыз',
        phone: 'Электрондық поштаңызды енгізіңіз',
        email: 'құпия сөзіңіз',
        pass:'Құпия сөз',
        confirmPass:'Құпия сөзді растаңыз',
        getPass:'Құпия сөзіңізді ұмыттыңыз ба?',
        login:'Кіру үшін',
        reg:'Тіркеу',
        acc:'Есептік жазбаңыз әлдеқашан бар ма?',
    }
}
if(lang =='ru'){

    pageContent ={
        name: 'Ваше имя',
        phone: 'Ваш телефон',
        email: 'Ваш email',
        pass:'Пароль',
        confirmPass:'Подтвердите пароль',
        getPass: 'Забыли пароль?',
        login:'Войти',
        reg:'Регистрация',
        acc:'Уже есть аккаунт?',
        
    }
}

const Register = ({form,setForm,error}) => {
    if(lang =='en'){
        pageContent ={
                name: 'Your Name',
                phone: 'Your phone number',
                email: 'Your email',
                pass:'Password',
                confirmPass:'Confirm password',
                getPass: 'Forgot password?',
                login:'Login',
                reg:'Sign in',
                acc:'Already have an account?',
            }
    }
    const onInputChange = (e) => {
        setForm({
            ...form,
            [e.target.name] : e.target.name === 'remember' ? e.target.checked : e.target.value
        })
    }

    return (
        <>
            <p>
                <Component.InputText name="name" placeholder={pageContent.name} width="100%" required onChange={onInputChange}/>
            </p>
            <p>
                <InputMask mask={'+7\\799 999 99 99'} formatChar={{
                    '9':'[0-9]'
                }} onChange={onInputChange}>
                    {(inputProps) =>  <Component.InputText name="phone_number" placeholder={pageContent.phone} width="100%" required/>}
                </InputMask>
            </p>
            <p>
                <Component.InputText name="email" placeholder={pageContent.email} width="100%" type="email" required onChange={onInputChange}/>
            </p>
            <p>
                <Component.InputText placeholder={pageContent.pass} name="password" width="100%" type="password" required onChange={onInputChange}/>
            </p>
            <p>
                <Component.InputText placeholder={pageContent.confirmPass} name="password_confirm" width="100%" type="password" required onChange={onInputChange} />
            </p>
            {
                error ?
                    <p className="errors">
                        <span>{error}</span>
                    </p>
                    : null
            }
            <p className="enter">
                <Component.Button padding="0.6rem 5rem">{pageContent.reg}</Component.Button>
            </p>
            <p className="forgot_alone">

                <Link to="/forget">
                    {pageContent.getPass}
                </Link>
            </p>
            <p className="noaccount">
                <span>{pageContent.getPass}</span>
                <Link to="/login">{pageContent.login}</Link>
            </p>
        </>
    )
}

export default AuthLayer(Register, {breadcump: pageContent.reg, title: pageContent.reg,action: registerAction});