import '../../style/components/_Delivery.scoped.sass';
import * as Component from '../../components/styled/card'
import {imgImport, conquerBreadcump, innerHtml, image_url} from '../../helpers/helper'
import {BreadCumps, Preloader, ScrollWrapper} from '../../components/global/Components';
import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";
import {deliverySelector} from "../../store/selectors";
import {deliveryPageAction} from "../../store/actions";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){

    pageContent ={
        main: 'Басты бет',
        delievery: 'Жеткізу',
        title:'Жеткізу және төлеу',
    
    }
}
if(lang =='ru'){

    pageContent ={
        main: 'Главная',
        delievery: 'Доставка',
        title:'Доставка и оплата',
    }
}
if(lang =='en'){
    pageContent ={
        main: 'Main page',
        delievery: 'Delievery',
        title:'Shipping and payment',
    }
}
const Delivery = () =>{
    const dispatch = useDispatch();
    const delivery = useSelector(deliverySelector);

    useEffect(() => {
        dispatch(deliveryPageAction()).then(res => {
            console.log(delivery)
        });
    },[]);

    return (
        <div className="delivery">
            <div className="container">
                <BreadCumps
                    items={conquerBreadcump([["/",`${pageContent.main}`],['/delivery',`${pageContent.delievery}`]])}
                />
                <Component.Container padding="2rem 0 5rem 0">
                    <div className="delivery_content">
                        <Component.Title margin="0 0 2.2rem 0">{pageContent.title}</Component.Title>
                        {
                            delivery['delivery'] ?
                                <div className="delivery_content_inner">
                                    <div>
                                        <img src={
                                            imgImport('delivery', 'delivery.jpg')
                                        } alt=""/>
                                    </div>
                                    <div>
                                        <Component.Title font="2.1rem" align="left">{delivery['delivery']['delivery']['title']}</Component.Title>
                                        <div dangerouslySetInnerHTML={innerHtml(delivery['delivery']['delivery']['description'])}>

                                        </div>
                                        <div>
                                            <Component.Title font="2.1rem" margin="1.5rem 0 0.3rem 0"
                                                             align="left">Оплата </Component.Title>
                                            <div dangerouslySetInnerHTML={innerHtml(delivery['delivery']['delivery']['price'])}>

                                            </div>
                                        </div>
                                    </div>
                                </div> : <Preloader/>
                        }
                    </div>
                </Component.Container>
            </div>
        </div>
    )
}
export default ScrollWrapper(Delivery);