import {BrowserRouter as Router,Route , Switch } from 'react-router-dom';

import MainPage from './pages/MainPage/MainPage';
import Catalog from './pages/Catalog/Catalog';
import Cabinet from "./pages/Cabinet/Cabinet";
import About from './pages/About/About';
import Detail from './pages/Detail/Detail';
import Discounts from './pages/Discounts/Discounts';
import Contacts from './pages/Contacts/Contacts';
import Basket from './pages/Basket/Basket';
import {Header,Footer} from './components/global/Components';
import Delivery from './pages/Delivery/Delivery';
import {Favorite} from './pages/Cabinet/Components/Favorite';
import {Profile} from './pages/Cabinet/Components/Profile';
import {History} from './pages/Cabinet/Components/History';
import Login from './pages/Auth/Login'
import {Order} from './pages/Order'
import Register from './pages/Auth/Register'
import {ForgetPassword} from './pages/Auth/ForgetPassword'
import "react-responsive-carousel/lib/styles/carousel.min.css";
import './style/global.sass';
import {Logout} from "./pages/Cabinet/Components/Logout";

const routes = [
  {
    path: "/cabinet",
    component: Cabinet,
    routes: [
      {
        path: "/cabinet/favorite",
        component: Favorite
      },
      {
        path: "/cabinet/profile",
        component: Profile
      },
      {
        path: "/cabinet/history",
        component: History
      },
      {
        path: "/cabinet/logout",
        component: Logout
      }
    ]
  }
];

function App() {
  return (
    <Router>
      <Header/>
      <Switch>
        <Route path="/" exact component={MainPage}/>
        <Route path="/catalog" component={Catalog}/>
        <Route path="/catalog/:id" component={Catalog}/>
        <Route path="/about" exact component={About}/>
        <Route path="/detail/:id" component={Detail}/>
        <Route path="/delivery" exact component={Delivery}/>
        <Route path="/discount" exact component={Discounts}/>
        <Route path="/contacts" exact component={Contacts}/>
        <Route path="/basket" exact component={Basket}/>
        <Route path="/login" exact component={Login}/>
        <Route path="/order" exact component={Order}/>
        <Route path="/register" exact component={Register}/>
        <Route path="/forget" exact component={ForgetPassword}/>
        {
          routes.map((route,i) => (
            <RouteWithSubRoutes key={i} {...route} />
          ))
        }
      </Switch>
      {/* "start": "react-scripts start", */}

      <Footer/>
    </Router>
  );
}

export function RouteWithSubRoutes(route) {
  return (
    <Route
      path={route.path}
      render={props => (
        // pass the sub-routes down to keep nesting
        <route.component {...props} routes={route.routes} />
      )}
    />
  );
}

export default App;
