import '../../style/components/_Auth.scoped.sass';
import * as Component from '../../components/styled/card'
import { BreadCumps,Custom_Basket } from '../../components/global/Components';
import { Link } from 'react-router-dom';
import {_storage} from "../../helpers/helper";
export const ForgetPassword = (props) => {
    const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
    
    let pageContent = {}
    if(lang =='kz'){
        pageContent ={
            main: 'Басты бет',
            logInAcc: 'Тіркелгіңізге кіріңіз',
            getPass:'Құпия сөзді қалпына келтіру',
            urLogin:'Логин, электрондық пошта немесе телефон',
            login:'Кіру үшін',
            send:'Жіберу',
            acc:'Есептік жазбаңыз бар ма?',
        }
    }
    if(lang =='ru'){
        pageContent ={
            main: 'Главная страница',
            logInAcc: "Вход в аккаунт",
            getPass:'Восстановить пароль',
            urLogin:'Ваш логин, email или телефон',
            login:'Войти',
            send:'Отправить',
            acc:'Есть аккаунт?',
            
        }
    }
    if(lang =='en'){
        pageContent ={
                main: 'Main Page',
                logInAcc: "Log-in",
                getPass:'Forgot password?',
                urLogin:'Your login, email or phone number',
                login:'Login',
                send:'Send',
                acc:'Already have an account?',
            }
        
    }
    return (
        <div className="auth">
            <div className="container">
                <BreadCumps 
                        items={
                            [
                                {
                                    link: "/",
                                    name: `${pageContent.main}`
                                },
                                {
                                    link: "/",
                                    name: `${pageContent.logInAcc}`
                                }
                            ]
                        }
                    />
                <Component.Title margin="2rem 0">{pageContent.getPass} </Component.Title>
                <Component.Container padding="0 0 4rem 0">
                    <div className="auth_inner">
                        <form action="">
                            <p>
                                <label htmlFor="">{pageContent.urLogin}</label>
                                <Component.InputText width="100%" type="email" placeholder="Email"/>
                            </p>
                            <p className="enter">
                                <Component.Button padding="0.6rem 5rem">{pageContent.send}</Component.Button>
                            </p>
                            <p className="noaccount">
                                <span>{pageContent.acc}</span>
                                <Link to="/login">{pageContent.login}</Link>
                            </p>
                        </form>
                    </div>
                </Component.Container>
            </div>
        </div>
    )
}