
/*
* custom importing image with require
* */
export const imgImport = (folder,img) => {
    return require(`./../assets/images/${folder}/${img}`).default
}
/* custom Breadcumps for every page with parameter
    @array ar;
 */
export const conquerBreadcump = (ar) => {
    const computFunc = (ar) => {
        return {
            link: ar[0],
            name: ar[1]
        }
    }
    return ar.reduce((prev, current, index) => {
        current = { ...computFunc(current)}
        prev = index === 1 ? {...computFunc(prev)} : prev

        return index === 1 ? [{...prev},{...current}] : [...prev, {...current}];
    })
}
/*
* innerHtml for setup html
* */
export const innerHtml = (res) => {
    return {__html: res}
}

/*
* get current page
* */
export const _curPage = window.location.href;


/*
* getter localstorage with setter
* */
export const _storage = {
    get : (key) => {
        return window.localStorage.getItem(key)
    },
    set: (key, value) => {
        return window.localStorage.setItem(key,value)
    },
    remove: (key) => {
        window.localStorage.removeItem(key);
    }
}

/**
 * get current api url
 */

export const _url = process.env.REACT_APP_API_URL + "/";

export const image_url = process.env.REACT_APP_IMAGE+"/";