/* eslint-disable react/jsx-pascal-case */
import '../../style/components/_MainPage.scoped.sass';
import * as Component from '../../components/styled/card';
import {imgImport, image_url, innerHtml} from '../../helpers/helper';
import { useEffect } from 'react';
import {Custom_Card, Preloader, ScrollWrapper} from '../../components/global/Components';
import { Carousel } from 'react-responsive-carousel';
import {useDispatch, useSelector} from "react-redux";
import {basketSelector, favoritesSelector, mainPageSelector} from "../../store/selectors";
import {getFavoriteAction, getStorageBasket, mainPageAction, mainPageTab} from "../../store/actions";
import {Link} from "react-router-dom";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){
    pageContent = {
        order:'Букетке тапсырыс беріңіз',
        best:'Біздің ең жақсы гүл шоқтары',
        all:'Бөлімнің барлық гүл шоқтары',
    }
}
if(lang =='ru'){
    pageContent = {
        order:'Заказать букет',
        best:'Наши лучшие букеты',
        all:'Все букеты отдела',
    }
}
if(lang =='en'){
    pageContent = {
        order:'Order a bouquet',
        best:'Our best bouquettes',
        all:'All bouqettes',
    }
}
const MainPage = () => {
    const contents = useSelector(mainPageSelector);
    const dispatch = useDispatch();
    const favorites = useSelector(favoritesSelector);
    const baskets = useSelector(basketSelector);

    useEffect(() => {
        dispatch(mainPageAction()).then(() => {
            dispatch(getStorageBasket());
            dispatch(getFavoriteAction());
        });
    },[]);

    const changeCategories = ({id,slug}) => {
        dispatch(mainPageTab(id,slug));
    }

    const checkIsFavorite = (obj) => {
        if(!Array.isArray(favorites.products )) {
            dispatch(getFavoriteAction());
            return false;
        }
        const cur = favorites.products.map((obj) => {
            return obj.id;
        });
        return cur.includes(obj.id);
    }

    return (
        <div>
            <div className="mainPage">
                <div className="container">
                    {
                        contents['contents'] !==null ?
                            <>
                                {/*Banner row*/}
                                <div className="mainPage_banner">
                                    <Carousel
                                        renderArrowPrev={(clickHandler, hasPrev, hasRight) => hasPrev && (
                                            <button onClick={clickHandler} className="arrow-left clickBtns">
                                                <img src={imgImport('slider', 'left-arrow.svg')}/>
                                            </button>
                                        )}
                                        renderArrowNext={(clickHandler, hasNext, hasRight) => hasNext && (
                                            <button onClick={clickHandler} className="arrow-right clickBtns">
                                                <img src={imgImport('slider', 'right-arrow.svg')}/>
                                            </button>
                                        )}
                                        showThumbs={false}
                                        renderThumbs={() => null}>
                                        {
                                            contents['contents'].slider.map((res,id) => {
                                                return (
                                                    <Component.BannerCard backgroundImage={image_url + res.image} key={id}>
                                                        <div className="inner_content">
                                                            <div>
                                                                <h2> {res.name}</h2>
                                                                <p dangerouslySetInnerHTML={innerHtml(res.description)}/>
                                                                <Component.Button>
                                                                    <Link to="/catalog">{pageContent.order}</Link>
                                                                </Component.Button>
                                                            </div>
                                                        </div>
                                                    </Component.BannerCard>
                                                )
                                            })
                                        }

                                    </Carousel>
                                </div>
                                {/* Deliveries row */}
                                <div className="mainPage_deliveries">
                                    <Component.TypeOfPay width="23%" margin="2.5rem 0 0 0 ">
                                        {
                                            contents['contents'].benefits.map((obj, index) => (
                                                <div className="inner" key={index}>
                                                    <img src={image_url + obj.image} alt=""/>
                                                    <p>
                                                        {obj.name}
                                                    </p>
                                                </div>
                                            ))
                                        }
                                    </Component.TypeOfPay>
                                </div>
                                {/* Cateogries row */}
                                <div className="mainPage_categories">
                                    {
                                        contents['contents'].cards.map((obj, index) => (
                                            <Component.CategoryInner image={image_url + obj.image} key={index}>
                                                <div>
                                                    {obj.name}
                                                </div>
                                            </Component.CategoryInner>
                                        ))
                                    }
                                </div>
                                {/* Best buckets */}
                                <div className="mainPage_favourites">
                                    <Component.Title margin="3.5rem 0 2.5rem 0">{pageContent.best}</Component.Title>
                                    <Component.Tabs>
                                        {
                                            contents['contents'].categories.map((obj, index) => (
                                                <div className={contents['tab'] === index ? 'active' : ''}
                                                     onClick={() => changeCategories({
                                                         id: index,
                                                         slug: obj.slug
                                                     })}
                                                     key={index}
                                                >
                                                    {obj.name}
                                                </div>
                                            ))
                                        }
                                    </Component.Tabs>
                                    <Component.CardsContainer>

                                        {
                                            contents['contents'].products ?
                                                !Array.isArray(contents['contents'].products) ?
                                                    contents['contents'].products.data.map((obj, id) => {

                                                        return (
                                                            obj.name ?
                                                                <div key={id} className="w-32">
                                                                    <Custom_Card params={
                                                                        {
                                                                            width: '32%',
                                                                            image: obj.image  ? image_url + obj.image : imgImport('about','about.jpg'),
                                                                            price: obj.price,
                                                                            title: obj.name,
                                                                            is_popular: obj.is_popular,
                                                                            discount: obj.discount
                                                                        }
                                                                    } isFavorite={favorites.products ? checkIsFavorite(obj) : false} index={obj.id} isActive={baskets.basket ? (baskets.basket[obj.id] === undefined) : true}/>
                                                                </div>: <div style={{width: "32%"}} key={id}/>

                                                        )
                                                    }) : contents['contents'].products.map((obj, id) => {

                                                        return (
                                                            obj.name ?
                                                                <div key={id} className="w-32">
                                                                    <Custom_Card params={
                                                                        {
                                                                            width: '32%',
                                                                            image: obj.image  ? image_url + obj.image : imgImport('about','about.jpg'),
                                                                            price: obj.price,
                                                                            title: obj.name,
                                                                            discount: obj.discount == null ? null : obj.discount + " T"
                                                                        }
                                                                    } isFavorite={favorites.products ? checkIsFavorite(obj) : false} index={obj.id} isActive={baskets.basket ? (baskets.basket[obj.id] === undefined) : true}/>
                                                                </div>: <div style={{width: "32%"}} key={id}/>

                                                        )
                                                    })
                                                : null
                                        }
                                    </Component.CardsContainer>
                                    <div className="mainPage_favourites_detail">
                                        <Link to="/catalog">
                                            <Component.Button shadow="none" background="white" margin="3rem 0"
                                                              border="2px solid #E9AAA6" color="#E9AAA6">{pageContent.all}</Component.Button>
                                        </Link>
                                    </div>
                                </div>
                                <div className="mainPage_about">
                                    <div className="mainPage_about_inner">
                                        <img src={image_url + contents['contents'].about.image} alt=""/>
                                        <div>
                                            <Component.Title>{ contents['contents'].about.name }</Component.Title>
                                            <p dangerouslySetInnerHTML={innerHtml(contents['contents'].about.description)}/>
                                        </div>
                                    </div>
                                </div>
                            </>
                            : <Preloader isStop={false}/>
                    }
                </div>
            </div>
        </div>
    )
}

export default ScrollWrapper(MainPage);