import * as Component from '../../../components/styled/card'
import {image_url, imgImport} from '../../../helpers/helper'
import {Custom_Card, Preloader} from '../../../components/global/Components';
import {useDispatch, useSelector} from "react-redux";
import {basketSelector, favoritesSelector} from "../../../store/selectors";
import {useEffect} from "react";
import {getFavoriteAction, getStorageBasket} from "../../../store/actions";
import {_storage} from "../../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){
    pageContent = {
        empty: 'Бос',
    }
}
if(lang =='ru'){
    pageContent = {
        empty: 'Пусто',
    }
}
if(lang =='en'){
    pageContent = {
        empty: 'Empty',
    }
}
export const Favorite = () => {
    const favorite = useSelector(favoritesSelector)
    const dispatch = useDispatch();
    const baskets = useSelector(basketSelector);
    useEffect(() => {
        dispatch(getStorageBasket());
        dispatch(getFavoriteAction());
    },[]);

    const checkIsFavorite = (obj,index) => {
        if(!Array.isArray(favorite.products )) {
            dispatch(getFavoriteAction());
            return false;
        }
        const cur = favorite.products.map((obj,index) => {
            return obj.id;
        });
        return cur.includes(obj.id);
    }

    const checkArray = () => {
        if(Array.isArray(favorite.products) && favorite.products.length === 0){
            return true;
        }
        return favorite.products && Array.isArray(favorite.products);
    }

    return (
        <div className="favorites">
            <Component.CardsContainer>
                {
                    checkArray() ? favorite.products.map((obj,index) => {
                        if(index < 3){
                            return (
                                <div key={index} className="w-32">
                                    <Custom_Card params={
                                        {
                                            width: '32%',
                                            image: obj.image ? image_url + obj.image : imgImport('catalog','card-1.jpg'),
                                            price: obj['price'],
                                            title: obj['name'],
                                            is_popular: obj.is_popular,
                                            discount: obj.discount
                                        }
                                    }
                                     margin="0"
                                     isFavorite={favorite.products ? checkIsFavorite(obj,index) : false}
                                     index={obj.id}
                                     isActive={baskets.basket ? (baskets.basket[obj.id] === undefined) : true}
                                    />
                                </div>
                            )
                        }
                        return (
                            <div key={index} className="w-32">
                                <Custom_Card params={
                                    {
                                        width: '32%',
                                        image: obj.image ? image_url + obj.image : imgImport('catalog','card-1.jpg'),
                                        price: `${obj['price']} ₸`,
                                        title: obj['name'],
                                        discount: `${obj['price']} ₸`
                                    }
                                }
                                 isFavorite={favorite.products ? checkIsFavorite(obj,index) : false}
                                 index={obj.id}
                                 isActive={baskets.basket ? (baskets.basket[obj.id] === undefined) : true}
                                />
                            </div>
                        )
                    }) : <span>{pageContent.empty}</span>
                }
            </Component.CardsContainer>
        </div>
    )
}