import * as Component from '../components/styled/card'
import {BreadCumps, DropDown, Preloader} from '../components/global/Components';
import {image_url, imgImport} from '../helpers/helper';
import {useEffect, useState} from 'react';
import InputMask from "react-input-mask";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import {useDispatch, useSelector} from "react-redux";
import {basketSelector, fetchBasketSelector, userSelector} from "../store/selectors";
import {
    fetchBasketsById,
    getStorageBasket,
    getUser,
    messageAction,
    orderAction,
    removeBasketProducts
} from "../store/actions";
import {useHistory} from "react-router-dom";
import {_storage} from "../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
