import '../../style/components/_Cabinet.scoped.sass';
import {Switch, useRouteMatch, useParams, Link, useLocation, useHistory} from 'react-router-dom';
import {BreadCumps} from '../../components/global/Components';
import { conquerBreadcump } from '../../helpers/helper';
import * as Component from '../../components/styled/card'
import { RouteWithSubRoutes } from '../../App';
import {useEffect, useMemo, useState} from 'react';
import {useSelector} from "react-redux";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){

    pageContent ={
        main:'Басты бет',
        personalInfo: 'Пайдаланушы деректері',
        fav: 'Таңдаулылар',
        orderHistory:'Тапсырыстар тарихы',
        exit:'Шығу',
        cabinet:'Кабинет'
    }
}
if(lang =='ru'){

    pageContent ={
        main:'Главная',
        personalInfo: 'Данные пользователя',
        fav: 'Избранное',
        orderHistory:'История заказов',
        exit:'Выйти',
        cabinet:'Кабинет'
    }
}
if(lang =='en'){
    pageContent ={
        main:'Main',
        personalInfo: 'User info',
        fav: 'Favorites',
        orderHistory:'Orders history',
        exit:'Exit',
        cabinet:'Cabinet'
    }
}
const Cabinet = ({routes}) =>{
    const { path} = useRouteMatch();
    const location = useLocation();
    const [nav,setNav] = useState([
        {
            link: `${path}/profile`,
            title: `${pageContent.personalInfo}`
        },
        {
            link: `${path}/favorite`,
            title: `${pageContent.fav}`
        },
        {
            title: `${pageContent.orderHistory}`,
            link :`${path}/history`
        },
        {
            title: `${pageContent.exit}`,
            link :`${path}/logout`
        }
    ]);
    const user = useSelector(state => state.isAuthed);
    const history = useHistory();

    const curLink = useMemo(() => {
        const filtered = nav.filter((obj,index) => obj.link === location.pathname);
        return filtered.length > 0 ? filtered[0] : {};
    },[location]);

    useEffect(() => {
        if(!user.isAuthenticated) history.push('/login')
    },[])

    return (
        <div className="cabinet">
            <div className="container">
                <BreadCumps 
                        items={conquerBreadcump([["/",pageContent.main],['/cabinet/profile',pageContent.cabinet ]])}
                />
                <Component.Container>
                    <Component.Title>{pageContent.cabinet}</Component.Title>
                    <div className="cabinet_menu">
                        <ul>
                            {
                                nav.map((obj, i) => (
                                    <li key={i} className={curLink.link == obj.link ? "activeLink" : ""}>
                                        <Link to={obj.link}>
                                            {obj.title}
                                        </Link>
                                    </li>
                                ))
                            }
                        </ul>
                        <Switch>
                            {routes.map((route, i) => (
                                <RouteWithSubRoutes key={i} {...route} />
                            ))}
                        </Switch>
                    </div>
                </Component.Container>
            </div>
        </div>
    )
}

export default Cabinet;