import axios from "axios";
import {_storage, _url} from "./helper";

axios.defaults.headers.common['content-language'] = 'ru';

class API {
    constructor() {
        this._setCustomUrl = function (url) {
            return _url + url;
        }
    }
    _postFilters(form, id) {
        console.log("FLITER FORM", form)
        // alert();
        return axios.post(this._setCustomUrl('products/filter?page='+id),form);
    }

    _fetchFavorites(){
        return axios.get(this._setCustomUrl('user/favorites '), {
            headers: {
                Authorization: 'Bearer ' + _storage.get('azalya_token')
            }
        });
    }
    _postFavorite(id){
        return axios.post(this._setCustomUrl(`products/${id}/favorites`), {}, {
            headers: {
                Authorization: 'Bearer ' + _storage.get('azalya_token')
            }
        });
    }

    _fetchUser(){
        return axios.get(this._setCustomUrl('user'), {
            headers: {
                Authorization: 'Bearer ' + _storage.get('azalya_token')
            }
        });
    }
    _updateUser(form){
        return axios.post(this._setCustomUrl('user'),form, {
            headers: {
                Authorization: 'Bearer ' + _storage.get('azalya_token')
            }
        });
    }
    _fetchOrderHistory(){
        return axios.get(this._setCustomUrl('user/orders '),{
            headers: {
                Authorization: 'Bearer ' + _storage.get('azalya_token')
            }
        })
    }
    _orderProducts(form){
        if(_storage.get('azalya_token')) return axios.post(this._setCustomUrl('checkout'),form,{
            headers: {
                Authorization: 'Bearer ' + _storage.get('azalya_token')
            }
        })
        // return axios.post(this._setCustomUrl('checkout '),form);
    }
    
    _fetchProductsById(ids) {
        return axios.post(this._setCustomUrl('products/get'), {products: ids});
    }
    _fetchFilters(){
        return axios.get(this._setCustomUrl('products/filter'),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _promotionPage(){
        return axios.get(this._setCustomUrl('page/promotions'),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _fetchMainPage() {
        return axios.get(this._setCustomUrl('page/home'),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _fetchProducts(id){
        return axios.get(this._setCustomUrl('page/products?page='+id),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _fetchDetailPage(id){
        return axios.get(this._setCustomUrl(`page/product/${id}`),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _fetchAboutPage(id){
        return axios.get(this._setCustomUrl('page/about'),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _fetchDeliveryPage(){
        return axios.get(this._setCustomUrl('page/delivery'),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _fetchContactsPage(){
        return axios.get(this._setCustomUrl('page/contacts'),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _changeCategory(id) {
        console.log("FROM API CAT", id)
        return axios.get(this._setCustomUrl(`categories/${id}`),{
            headers:{
                'content-language':_storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
            }
        });
    }
    _register(form){
        console.log(form)
        return axios.post(this._setCustomUrl('auth/register'),form);
    }
    _login(form){
        return axios.post(this._setCustomUrl('auth/login'),form);
    }
}

const _api = new API();

export default _api;