/*
* Header
* */
export const Header = () =>{
    const [isHeader,setHeader] = useState(false);
    const user = useSelector(authSelector);
    const message = useSelector(state => state.messageAlert);
    const dispatch = useDispatch();
    const history = useHistory();
    const storageBasket = useSelector(basketSelector);
    useEffect(() => {
        dispatch(getStorageBasket());
    },[]);
    const changePage = (e,page) => {
        e.preventDefault();
        history.push(page);
        setHeader(false);
    }
    const changeLanguage = (e) =>{
        changeLang(e.target.textContent)
    }
    return (
        <div id="header_top">
            <div className="container">
                {
                    message ?
                        <div className="message_alert">
                            <span className={message['status']}>{message['message']}</span>
                        </div> : null
                }
                <header className="header">

                    <div className="header_inner">
                        <div className="header_logo">

                            <Link to="/">
                                <img src={ headerLogo } alt="" />
                            </Link>

                        </div>

                        <div className="header_nav">

                            <div className="burger burger_mobile" onClick={() => setHeader(!isHeader)}>
                                <div/>
                                <div/>
                                <div/>
                            </div>

                            <div className="header_nav_item"><Link to="/catalog">{pageContent.catalog}</Link></div>
                            <div className="header_nav_item"><Link to="/discount">{pageContent.discounts}</Link></div>
                            <div className="header_nav_item"><Link to="/about">{pageContent.aboutUs}</Link></div>
                            <div className="header_nav_item"><Link to="/delivery">{pageContent.delievery}</Link></div>
                            <div className="header_nav_item"><Link to="/contacts">{pageContent.contacts}</Link></div>
                            
                            </div>
                            <div className="header_phone"><a href="tel:+88008008000">+8 800 800 8000</a></div>

                            <div className="header_language">
                                <a style={{cursor:'pointer'}} onClick={changeLanguage}>eng</a>
                                <a style={{cursor:'pointer'}} onClick={changeLanguage}>rus</a>
                                <a style={{cursor:'pointer'}} onClick={changeLanguage}>kaz</a>
                            </div>

                                <Link to="/basket" className="basketLink">
                                    <img className="basket_img" src={imgImport('header','basket.svg')}/>
                                    {
                                        storageBasket.basket ? <span className="items">{Object.keys(storageBasket.basket).length }</span> : null
                                    }
                                </Link>
                                <Link to={user.isAuthenticated ? '/cabinet/profile' : '/login'}>
                                    <img className="profile_img" src={imgImport('header','Profile.svg')}/>
                                </Link>
                            </div>

                            <>

                                 <div className={(isHeader ? 'collapsable' : '') + " header_nav_mobile"}>
                                     <div className="burger" onClick={() => setHeader(!isHeader)}>
                                         <div/>
                                         <div/>
                                     </div>
                                    <div className="header_nav_mobile_item"><Link to="/catalog/1" onClick={e => changePage(e,'/catalog')}>Каталог</Link></div>
                                    <div className="header_nav_mobile_item"><Link to="/discount" onClick={e => changePage(e,'/discount')}>Акции</Link></div>
                                    <div className="header_nav_mobile_item"><Link to="/about" onClick={e => changePage(e,'/about')}>О нас</Link></div>
                                    <div className="header_nav_mobile_item"><Link to="/delivery" onClick={e => changePage(e,'/delivery')}>Доставка</Link></div>
                                    <div className="header_nav_mobile_item"><Link to="/contacts" onClick={e => changePage(e,'/contacts')}>Контакты</Link></div>
                                    <div className="header_phone"><a href="tel:+88008008000">+8 800 800 8000</a></div>
                                </div>
                            </>
                </header>
            </div>
        </div>
    )
}


/*
* BreadCump
* */
export const BreadCumps = (props) => {
    const {
        items
    } = props;
    return (
        <div className="breadcumps">
            {items.map((obj, index) => (
                <div className="breadcumps_inner" key={index}>
                    <Link to={obj.link}>
                        <span>{ obj.name }</span>
                        {(index + 1) !== items.length ? <img src={imgImport('slider','right-arrow.svg')} alt="" /> : ""}
                    </Link>
                </div>
            ))}
        </div>
    )
}

/*
* Custom card
* */
export const Custom_Card = (props) => {
    const {params,isStock,margin,index,isActive = true,isFavorite=false} = props;
    const user = useSelector(authSelector);
    const dispatch = useDispatch();
    const history = useHistory();
    const clickFavorite = () => {
        if(!user.isAuthenticated) {
            dispatch(messageAction({
                message: "Пожалуйста авторизуйтесь",
                status: "error"
            }));
            history.push('/login');
        }
        dispatch(postFavoriteAction(index))
            .then(res => dispatch(getFavoriteAction()))
            .then(res => dispatch(messageAction({
                message: !isFavorite ? "Добавлено в избранное!" : "Удалено",
                status: !isFavorite ? "success" : "error"
            })));
    }
    return(
        <Component.Card width="100%" margin={margin} key={index}>
            <img src={params.image} alt="" />
            {
                isStock ? 
                <>
                    <div className="content content_stock" key={index}>
                        <span className="title">
                            {params.title}
                        </span>
                        <div className="inner">
                            <div>
                                <span className="description">
                                    {params.description}
                                </span>
                            </div>
                        </div>
                        <Component.Button padding="0.6rem 3.6rem">
                            <a href="https://wa.me/+77751915522" target="_blank" rel="noreferrer">
                            {pageContent.more}
                            </a>
                        </Component.Button>
                    </div>
                </>
                : <>
                    <div className="content" key={index}>
                        <span>
                            <Link to={`/detail/${index}`}>{params.title}</Link>
                        </span>
                        
                        <div className="inner">
                            <div>
                                {
                                    params.discount ? <span className="price">
                                            {params.price - Math.round((parseInt(params.price) * parseInt(params.discount)) / 100)}₸
                                        </span> : <span className="price">
                                    {params.price}₸
                                </span>
                                }
                                {
                                    params.discount ? <span className="discount">{params.price}₸</span> : null
                                }
                            </div>
                            <img src={isFavorite ? imgImport('catalog', 'heart_thick.png') : imgImport('catalog', 'heart.png')} alt="" onClick={() => clickFavorite()}/>
                        </div>
                        {
                            isActive ?
                                <Link to={`/detail/${index}`}>
                                    <Component.Button padding="0.6rem 2.6rem">{pageContent.basket}</Component.Button>
                                </Link> :
                                <Component.Button padding="0.6rem 2.6rem" background="white" border="none" color="#E9AAA6">{pageContent.alreadyInBasket}</Component.Button>
                        }
                    </div>
                    <div className="hints">

                        {
                            params ? (params['is_popular'] ?  <span>Хит</span> : null) : null
                        }
                        {
                            params ? (params['discount'] ? <span>-{params['discount']}%</span> : null) : null
                        }

                    </div>
                </>
            }
        </Component.Card>
    )
}

/*
* Preloader to show loading bar
* */
export const Preloader = ({isStop}) => {

    return (
        <div className='custom_preloader'>
            <Component.Preloader isStop={isStop}><div/></Component.Preloader>
        </div>
    )
};

/*
* Scroll Wrapper to scroll automatically to top of page
* */
export const ScrollWrapper = (TargetComponent) => {
    class ScrollWrapperClass extends React.Component{
        componentWillMount() {
            window.scrollTo(0,0);
        }

        render(){
            return <>
                <TargetComponent/>
                </>
        }
    }
    return ScrollWrapperClass;
}

/*
* Auth Layer HOC to auth
* */
export const AuthLayer = (TargetComponent, {breadcump,title,action}) => {

    function Layer(){
        const isUser = useSelector(state => state.isAuthed);
        const history = useHistory();

        const [preloader,setPreloader] = useState({isStop: true});
        const [error,setError] = useState(undefined);
        const router = useHistory();
        const dispatcher = useDispatch();
        const [form, setForm] = useState({
            email: '',
            password: '',
            remember: 0
        });

        useEffect(() => {
            // eslint-disable-next-line no-unused-vars
            let mounted = false;
            if(isUser.isAuthenticated) history.push('/');
            return () => mounted = true;
        })

        const validation = () => {
            if(title === 'Вход' || title === 'Забыли пароль') return false;

            if(form.password !== form.password_confirm) return "Пароли не совпадают";

            if(form.password.length <= 6) return "Пароль должен быть длиной больше 6 ти букв"

            return false;
        }

        const auth = (e) =>{
            e.preventDefault();
            setPreloader({isStop: false});

            if(validation()){
                setError(validation());
                setPreloader({isStop: false});
                return;
            }

            dispatcher(action(form))
                .then(response => {
                    setPreloader({isStop: true});
                    if(title === 'Регистрация') router.push('/login');
                    if(title === 'Вход') router.push('/cabinet/profile');
                    setPreloader({isStop: true});
                })
                .catch(err => {
                    setError(err.response.data.message ? err.response.data.message : "Произошла ошибка");
                    setPreloader({isStop: true});
                });
        }
        return(
            <div className="auth" >
                <div className="container">
                    <BreadCumps
                        items={
                            [
                                {
                                    link: "/",
                                    name: "Главная"
                                },
                                {
                                    link: "/",
                                    name: breadcump
                                }
                            ]
                        }
                    />
                    <Component.Title margin="2rem 0">{title}</Component.Title>
                    <Component.Container padding="0 0 4rem 0">
                        <Preloader isStop={preloader.isStop}/>
                        <div className="auth_inner">
                            <form action="" onSubmit={auth}>
                                <TargetComponent form={form} setForm={setForm} error={error}/>
                            </form>
                        </div>
                    </Component.Container>
                </div>
            </div>
        );
    }

    return Layer;
}
/*
* Baskey Layer
* */
export const BasketLayer = (Component) =>{
    function Layer(){
        const user = useSelector(state => state.isAuthed);
        const history = useHistory();
        useEffect(() => {
           if(!user.isAuthenticated) history.push('/');
        },[])
        return(
            <div>
                <Component/>
            </div>
        )
    }
    return Layer;
}

/*
* Basket
* */
export const Custom_Basket = (props) => {
    const { headers, items = [], isBasket, cachedBasket, removeProduct } = props;
    console.log("PROPS", props)
    const computePrices = () => {
        if(items.length === 0) return {price: 0};
        // eslint-disable-next-line array-callback-return
        return items.reduce((prev,next) => ({price : prev.price + next.price}))
    }
    return (
        <div className="basket">
            <div className="basket_header">
                {
                    headers ? headers.map((obj,index) =>{
                        return (
                            <div key={index}>
                                <span>{ obj }</span>
                            </div>
                        )
                    }) : null
                }
            </div>
            <div className="basket_products">
            
                {
                    items ? items.map((obj,index) =>{
                        return (
                            <div className="basket_products_inner" key={index}>
                                <div className="product_content">
                                    {isBasket ? 
                                    <>
                                        <img src={obj.image ? image_url + obj.image : imgImport('about','about.jpg')} alt="" />
                                        <div>
                                            <span>{obj.name}</span><br/>
                                            <span>{obj.category.name}</span>
                                        </div>
                                    </> : 
                                    <> 
                                        <span>{obj.code}</span><br/>
                                    </>}
                                </div>
                                {
                                    isBasket ? 
                                    <>
                                        <div>
                                            <span className="price">{obj.price}₸</span>
                                        </div>
                                        <div>
                                            <span className="quantity">{cachedBasket.basket[obj.id] ? cachedBasket.basket[obj.id].count : 0}</span>
                                        </div>
                                        <div>
                                            <span className="total">{obj.price}₸</span>
                                        </div>
                                        <div>
                                            <span style={{"cursor": "pointer"}} onClick={() => removeProduct(obj.id)}><img src={imgImport('basket','deleteIcon.svg')} alt="" /></span>
                                        </div>
                                    </> : 
                                    <>
                                        <div>
                                            <span className="quantity">{obj.count}</span>
                                        </div>
                                        <div>
                                            <span className="price">{obj.date}</span>
                                        </div>
                                        <div>
                                            <span className="price">{obj.status}</span>
                                        </div>
                                        <div>
                                            <span className="total">{obj.total_price}₸</span>
                                        </div>
                                    </>
                                }
                            </div>
                        )
                    }):null
                }
            </div>
            <div className="basket_btns">
                {
                    isBasket ? (
                        <div className="basket_btns_inner">
                            <div className="total">
                                <span>{pageContent.summary}:</span>
                                <span className="total_price">{computePrices().price} ₸</span>
                            </div>
                            <div className="basket_btns_inner_links">
                                <Component.Button><Link to="/order">{pageContent.summary}</Link></Component.Button>
                                <Link to="/catalog">{pageContent.continue}</Link>
                            </div>
                        </div>
                    ) : null
                }
            </div>
        </div>
    )
}
/*
* Input dropdown
* */
export const DropDown = ({list,keyIndex="address",defaultTitle,chosen={},setChosen = () => {}}) =>{
    const [maxHeight,setHeight] = useState(0);
    const openDropDown = () => {
        if(maxHeight === 0) setHeight("10000px")
        else return setHeight(0)
    }
    const choseAr = (res) => {

        setChosen({...chosen,[keyIndex]: res});
        setHeight(0);
    }
    return (
        <Component.DropDownContainer maxHeight={maxHeight}>
            <div className="dropdown_current" onClick={openDropDown}>
                <span>{chosen.time ? chosen.time : defaultTitle}</span>
                <img src={imgImport('default_text','arrow_bottom.svg')} alt="" />
            </div>
            <div className="dropdown_list">
                {
                    list.map((res,i) => (
                        <div key={i} onClick={() => choseAr(res)}>
                            <span>{res}</span>
                        </div>
                    ))
                }
            </div>
        </Component.DropDownContainer>
    )
}
