import '../../style/components/_About.scoped.sass';
import * as Component from '../../components/styled/card'
import {imgImport, conquerBreadcump, innerHtml, image_url} from '../../helpers/helper'
import {useEffect, useState} from 'react';
import {BreadCumps, Preloader, ScrollWrapper} from '../../components/global/Components';
import {useDispatch, useSelector} from "react-redux";
import {aboutPageAction} from "../../store/actions";
import {aboutSelector} from "../../store/selectors";
import {_storage} from "../../helpers/helper";
const About = () =>{
    const dispatch = useDispatch();
    const about = useSelector(aboutSelector);
    const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
    
    let pageContent = {}
    if(lang =='kz'){
        pageContent ={
        
                main: 'Басты бет',
                abUs: 'Біз туралы',
                adv:'Біздің артықшылығымыз',
                employees:'Біздің қызметкерлер'

        }
    }
    if(lang =='ru'){
        pageContent ={
                main: 'Главная страница',
                abUs: 'О нас',
                adv:'Наши преимущества',
                employees:'Наши сотрудники'
            
        }
    }
    if(lang =='en'){
        pageContent ={
                main: 'Main Page',
                abUs: 'About us',
                adv:'Our advandages',
                employees:'Our employees'
            }
        
    }
    useEffect(() => {
        if(!about['about'])
            dispatch(aboutPageAction());
    },[])
    return (
        <>
            <div className="about">
                <div className="container">
                    <BreadCumps 
                        items={conquerBreadcump([["/",`${pageContent.main}`],["/about",`${pageContent.abUs}`]])}
                    />
                    {
                        about['about'] ?
                            <>
                                <Component.Container padding="2rem 0 5rem 0">
                                    <Component.Title margin="0 0 2rem 0">{pageContent.abUs}</Component.Title>
                                    <div className="about_content">
                                        <div className="image">
                                            <img src={about['about']['about']['image'] ? about['about']['about']['image'] : imgImport("about","about.jpg")} alt="" />
                                        </div>
                                        <div dangerouslySetInnerHTML={innerHtml(about['about']['about'].description)}/>
                                    </div>
                                    <div className="about_categories" >
                                        <Component.Title margin="4rem 0 0 0">{pageContent.adv}</Component.Title>
                                        <div>
                                            <Component.TypeOfPay width="18%" margin="1rem 0">
                                                {
                                                    about['about']['benefits'].map((obj,index) => (
                                                        <div className="inner" key={index}>
                                                            <img src={image_url + obj.image} alt="" />
                                                            <p>
                                                                {obj.name}
                                                            </p>
                                                        </div>
                                                    ))
                                                }
                                            </Component.TypeOfPay>
                                        </div>
                                    </div>
                                    <div className="about_workers">
                                        <Component.Title margin="4rem 0 0 0">{pageContent.employees}</Component.Title>
                                        <div className="about_workers_inner">
                                            {
                                                about['about']['members'].length === 0 ? <span style={{margin: "0 auto"}}>Не найдено</span>
                                                    : about['about']['members'].map((obj,index) => {
                                                            return (
                                                                <div key={index}>
                                                                    <img src={obj.image ? image_url + obj.image :imgImport('team','team-1.jpg')} alt="" />
                                                                    <h3>{obj.fullname}</h3>
                                                                    <span>{obj.position}</span>
                                                                </div>
                                                            )
                                                        })
                                            }
                                        </div>
                                    </div>
                                </Component.Container>
                            </>: <Preloader/>
                    }
                </div>
            </div>
        </>
    )
}
export default ScrollWrapper(About);