import '../../style/components/_Contacts.scoped.sass';
import * as Component from '../../components/styled/card'
import {conquerBreadcump, image_url, imgImport, innerHtml} from '../../helpers/helper'
import {BreadCumps, Preloader, ScrollWrapper} from '../../components/global/Components';
import {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {contactsSelector} from "../../store/selectors";
import {contactsPageAction} from "../../store/actions";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'

const Contacts = () =>{
    let pageContent = {}
    if(lang =='kz'){    
        pageContent ={
            contacts: 'Контактілер',
            main:'Басты бет'
        }
    }
    if(lang =='ru'){

        pageContent ={
            contacts: 'Контакты',
            main:'Главная'
        }
    }
    if(lang =='en'){
        pageContent ={
                contacts: 'Contacts',
                main:'Main page'
            }
    }
    const contacts = useSelector(contactsSelector);
    const dispatch = useDispatch();

    useEffect(() => {
        if(!contacts.contacts) dispatch(contactsPageAction());
    },[]);
    return (
        <div className="contacts">
            <div className="container">
                <BreadCumps 
                    items={conquerBreadcump([["/",`${pageContent.main}`],['/contacts',`${pageContent.contacts   }`]])}
                />
                <Component.Container padding="2rem 0 5rem 0">
                    <Component.Title>{pageContent.contacts}</Component.Title>
                    {
                        contacts['contacts'] ?
                            <>
                                <Component.TypeOfPay margin="0 0 0 0" width="25%" height="140px" cardWidth="100%" imgHeight="35px">
                                    {
                                        contacts['contacts'].contacts.map((obj,index) => (
                                            <div className="inner" key={index}>
                                                <img src={image_url + obj.image} alt="" />
                                                <p className="inner_title">
                                                    {obj.name}
                                                </p>
                                                <div className="inner_detail" dangerouslySetInnerHTML={innerHtml(obj.value)}/>
                                            </div>
                                        ))
                                    }
                                </Component.TypeOfPay>
                                <div dangerouslySetInnerHTML={innerHtml(contacts['contacts']['map'])}/>
                            </>:<Preloader/>
                    }
                </Component.Container>
            </div>
        </div>
    )
}
export  default ScrollWrapper(Contacts);