/*
 *  Alert message
 */
export const messageAction = (obj) => async (dispatch) => {
    try{
        dispatch({
            type: actions.MESSAGE_ALERT,
            payload: obj
        })

        const timer = setTimeout(() => {
            dispatch({
                type: actions.MESSAGE_ALERT,
                payload: {message: null, status: null}
            });
            clearTimeout(timer);
        },3000);

        return Promise.resolve(obj);
    }
    catch (err) {
        return Promise.reject(err);
    }
}

