/*
* Add product to basket
 */
export const addBasket = (data) => async (dispatch) => {
    try{
        dispatch({
            type: actions.ADD_BASKET_ACTION,
            payload: data
        });
        let basket_products = _storage.get('azalya_basket') ? _storage.get('azalya_basket') : "{}";
        basket_products = JSON.parse(basket_products);
        basket_products = {...basket_products, ...data};

        _storage.set('azalya_basket', JSON.stringify(basket_products));

        return Promise.resolve(data);
    }
    catch (err) {
        return Promise.reject(err);
    }
}

/*
* GET basket products
 */
export const getStorageBasket = () => async (dispatch) => {
    try{
        const parsedStorage = _storage.get('azalya_token') !== undefined ? JSON.parse(_storage.get('azalya_basket')) : null;
        dispatch({
            type: actions.FETCH_BASKET,
            payload: parsedStorage
        });

        return Promise.resolve(parsedStorage);
    }
    catch (err) {
        return Promise.reject(err);
    }
}
/*
* Remove from basket the product
 */
export const removeProductFromBasket = (id) => async (dispatch) => {
    try{
        const parsedStorage =_storage.get('azalya_basket') ? JSON.parse(_storage.get('azalya_basket')) : null;
        if(!parsedStorage){
           return  Promise.resolve(null);
        }
        delete parsedStorage[id];

        _storage.set('azalya_basket',JSON.stringify(parsedStorage));

        dispatch({
            type: actions.REMOVE_PRODUCT,
            payload: parsedStorage
        })

        return Promise.resolve(parsedStorage);
    }
    catch (err) {
        return Promise.reject(err);
    }
}

