export const changeLang = (lang) =>{
    let language
    if(lang == 'eng'){
        language = 'en'
    }
    if(lang == 'rus'){
        language = 'ru'
    }
    if(lang == 'kaz'){
        language = 'kz'
    }
    let changingLang = new Promise((resolve, reject) => {
        _storage.set('current_lang',language)
        resolve()
    })
    changingLang.then(()=>{
        window.location.reload()
    })
}