import '../../style/components/_Discounts.scoped.sass';
import * as Component from '../../components/styled/card'
import {imgImport, conquerBreadcump} from '../../helpers/helper'
import {BreadCumps, Custom_Card, Preloader, ScrollWrapper} from '../../components/global/Components';
import {useDispatch, useSelector} from "react-redux";
import {promotionSelector} from "../../store/selectors";
import {useEffect} from "react";
import {promotionPageAction} from "../../store/actions";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'
let pageContent = {}
if(lang =='kz'){

    pageContent ={
        main: 'Басты бет',
        discounts: 'Қор',
    }
}
if(lang =='ru'){

    pageContent ={
        main: 'Главная',
        discounts: 'Акции',
    }
}
if(lang =='en'){
    pageContent ={
        main: 'Main page',
        discounts: 'Discounts',
    }
}
const Discounts = () =>{
    const dispatch = useDispatch();
    const promotion = useSelector(promotionSelector);

    useEffect(() => {
        dispatch(promotionPageAction());
    },[])

    return (
        <div className="delivery">
            <div className="container">
                <BreadCumps 
                    items={conquerBreadcump([["/",`${pageContent.main}`],['/discounts',`${pageContent.discounts}`]])}
                />
                <Component.Container padding="2rem 0 5rem 0">
                    {promotion['promotion'] ?
                        <>
                            <Component.Title margin="0 0 0 0">{pageContent.discounts}</Component.Title>
                            <Component.CardsContainer>
                                {promotion['promotion']['promotions'].data.map((obj, index) => {
                                    return (
                                        // eslint-disable-next-line react/jsx-pascal-case
                                        <div key={index} className="w-48">
                                            <Custom_Card params={
                                                {
                                                    width: '48%',
                                                    image: imgImport('stock', 'stock-1.jpg'),
                                                    description: `
                                                Мы запустили акционное предложение на букеты со скидкой до 35% «Яркое лето- яркие скидки»
Скидка действует по всем городам Казахстана. Дарите яркое, летнее настроение своим близким людям
                                                `,
                                                    title: `
                                                Скидка 35 % на второй букет
                                                `,
                                                }
                                            } isStock={true}/>
                                        </div>
                                    )
                                })
                                }
                            </Component.CardsContainer>
                        </>
                        : <Preloader/>
                    }
                </Component.Container>
            </div>
        </div>
    )
}
export default ScrollWrapper(Discounts);
